.project-page {
    width: 100%;
    min-height: calc(100vh - 80px);
  }
  
  .project-container {
    max-width: 80rem;
    margin: 0 auto;
    padding: 2rem 1.5rem;
  }
  
  .project-hero {
    text-align: center;
    margin: 4rem 0 6rem;
  }
  
  .project-hero h1 {
    font-size: 4rem;
    font-weight: 600;
    color: #000000;
    margin-bottom: 2rem;
  }
  
  .project-subtitle {
    font-size: 1.25rem;
    line-height: 1.6;
    color: #000000;
    max-width: 48rem;
    margin: 0 auto;
  }
  
  .project-main-image {
    width: 100%;
    margin-bottom: 6rem;
  }
  
  .hero-image {
    width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  .project-info-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    margin: 0 auto;
    max-width: 90%;
  }
  
  .info-section {
    padding: 1rem;
    border-radius: 0.5rem;

  }
  
  .info-title {
    font-size: 1.5rem;
    font-weight: 600;
    color: #000000;
    margin-bottom: 1rem;
    position: relative;
  }
  
  .info-title::after {
    content: '';
    position: absolute;
    bottom: -0.5rem;
    left: 0;
    width: 2rem;
    height: 2px;
    background-color: #2563eb;
  }
  
  .info-text {
    font-size: 1rem;
    line-height: 1.6;
    color: #000000;
  }
  
  @media (max-width: 1024px) {
    .project-info-grid {
      grid-template-columns: repeat(2, 1fr);
      max-width: 100%;
    }
  }
  
  @media (max-width: 768px) {
    .project-container {
      padding: 1rem;
    }
  
    .project-hero h1 {
      font-size: 3rem;
    }
  
    .project-subtitle {
      font-size: 1.125rem;
    }
  
    .project-info-grid {
      grid-template-columns: 1fr;
      gap: 1.5rem;
    }
  
    .info-section {
      padding: 1.5rem;
    }
  }

  .video-section {
    margin: 5rem 0 6rem auto;
    width: 80%;
  }
  
  .video-title {
    font-size: 2rem;
    font-weight: 600;
    color: #000000;
    margin-bottom: 2rem;
    text-align: center;
  }
  
  .video-container {
    position: relative;
    width: 100%;
    padding-top: 56.25%; /* 16:9 Aspect Ratio */
    background-color: #f3f4f6;
    border-radius: 8px;
    overflow: hidden;
  }
  
  .youtube-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
    border-radius: 8px;
  }
  
  @media (max-width: 768px) {
    .video-section {
      margin: 3rem 0;
    }
  
    .video-title {
      font-size: 1.5rem;
      margin-bottom: 1.5rem;
    }
  }

.gallery-section {
    margin: 0;
    width: 100%;
  }
  
  .gallery-title {
    font-size: 2rem;
    font-weight: 600;
    color: #000000;
    margin-bottom: 3rem;
    text-align: center;
  }
  
  .gallery-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    width: 100%;
  }
  
  .gallery-item {
    position: relative;
    width: 100%;
    padding-top: 56.25%; /* 16:9 aspect ratio */
    background-color: #f3f4f6;
    border-radius: 8px;
    overflow: hidden;
  }
  
  .gallery-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
  }
  
  @keyframes imageLoad {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  .gallery-image {
    animation: imageLoad 0.5s ease-in;
  }
  
  @media (max-width: 1024px) {
    .gallery-section {
      margin: 4rem 0;
    }
    
    .gallery-grid {
      grid-template-columns: repeat(3, 1fr);
      gap: 10px;
    }
  }
  
  @media (max-width: 768px) {
    .gallery-section {
      margin: 3rem 0;
    }
  
    .gallery-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  
  .gallery-title {
    font-size: 2rem;
    font-weight: 600;
    color: #000000;
    margin-bottom: 3rem;
    text-align: center;
    text-transform: uppercase;
  }
  }
  
  
  .gallery-image:focus {
    outline: 2px solid #2563eb;
    outline-offset: 2px;
  }

/* Added from Project_Catalog.css */
.cherge-page-wrapper.palter-page {
  padding: 0;
}

.cherge-page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  box-sizing: border-box;
  background-color: #f0f0f0;
  color: #000;
}

.cherge-main-content {
  flex-grow: 1;
  width: 100%;
}

.unified-initials-section {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 2rem;
  margin-bottom: 2rem;
}

.unified-initials-text-content {
  flex: 1;
}

.unified-initials-image-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.unified-initials-image {
  width: 100%;
  height: auto;
  border-radius: 20px;
}

.project-catalog-title {
  font-weight: 500;
  line-height: 1;
  color: #000;
  text-align: left;
  margin-top: 0;
  font-size: 3vw;
}

.cherge-specific-title {
  margin-bottom: 0 !important;
}

.project-catalog-subtitle {
  font-size: 1.5rem;
  color: #000000;
  text-align: left;
  margin-bottom: 4em;
  max-width: 800px;
  line-height: 1.3;
  font-weight: 500;
}

.cherge-detail-item {
  font-weight: 500;
  line-height: 1;
  color: #000;
  text-align: left;
  font-size: 3vw;
}

.catalog-projects-overview-section {
  margin-top: 4rem;
  margin-bottom: 4rem;
  padding-right: 0;
}

@media (max-width: 768px) {
  .unified-initials-section {
    flex-direction: column;
  }
}
