.blog-container {
  padding: 2rem 0;
  font-family: sans-serif;
  color: #333;
  background-color: #eeeeee;
}

.blog-container h1,
.subtitle,
.blog-content {
  box-sizing: border-box;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 2rem;
  padding-right: 2rem;
}

.blog-container h1 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.subtitle {
  font-size: 1.2rem;
  color: #000000;
  margin-bottom: 2rem;
}

.blog-content p {
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.blog-content strong {
  font-weight: bold;
}

.blog-content ul, .blog-content ol {
  margin-left: 2rem;
  margin-bottom: 1.5rem;
}

.blog-content li {
  margin-bottom: 0.5rem;
}

.blog-image {
  width: 100%;
  height: auto;
}
