/* Page Wrapper and Main Content Styles (copied from Home.css and adapted) */
body {
  background-color: #f0f0f0;

}

.cherge-page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding: 20px; /* General padding */
  box-sizing: border-box;
  background-color: #f0f0f0;
  color: #000; /* Assuming base text color */
}

.cherge-main-content {
  flex-grow: 1;
  width: 100%;
}

/* Text Details Styles (copied from Home.css and adapted) */
.cherge-text-details {
  display: flex;
  flex-direction: column; /* Stack items vertically */
  width: 100%; /* Ensure this container takes full width */
  padding-bottom: 10px; /* Keep or adjust as needed */
  margin-top: 3em; /* Increased space above this container */
  /* border-bottom: 1px solid #ccc; */ /* Border removed as per request */
}

/* Styles for the individual detail items (previously p, now h3.cherge-detail-item) */
.cherge-detail-item {
  font-weight: 500; /* Copied from .project-catalog-title */
  line-height: 1; /* Adjusted for readability */
  color: #000; /* Copied from .project-catalog-title */
  text-align: left; /* Copied from .project-catalog-title */
  font-size: clamp(24px, 2.5vw, 48px); /* Responsive font size with min/max */
}

/* Commenting out old .cherge-text-details p as it's no longer used for these items
.cherge-text-details p {
  font-size: 14px;
  text-transform: uppercase;
  margin: 0; 
}
