/* Existing Cherge.css styles below */
.unified-initials-section {
  display: flex; /* Enable flexbox */
  flex-direction: row; /* Ensure items are laid out in a row */
  align-items: flex-start; /* Align items to the top */
  gap: 2rem; /* Space between text and image */
  margin-bottom: 2rem; /* Space below the section */
}

.unified-initials-text-content {
  flex: 1; /* Text content takes 1/3 space */
}

.unified-initials-image-container {
  flex: 1; /* Image content takes 2/3 space */
  display: flex;
  justify-content: center;
  align-items: center;
}

.unified-initials-image {
  width: 100%; /* Make image fill its container's width */
  height: auto;
  border-radius: 20px; /* Optional: if you want rounded corners */

}

.project-catalog-title {
  font-weight: 500; /* Copied from home-main-text */
  line-height: 1; /* Copied from home-main-text */
  color: #000; /* Copied from home-main-text */
  text-align: left; /* Copied from home-main-text */
  margin-top: 0; /* Adapted from home-main-text (margin: 0) */
  font-size: clamp(24px, 2.5vw, 48px); /* Responsive font size with min/max */
  margin-bottom: 0.5em; /* Existing: Add some space below the title */
  padding-bottom: 0.3em;
}

.cherge-specific-title {
  margin-bottom: 0 !important; /* Remove bottom margin specifically for Cherge page */
}

.project-catalog-subtitle {
  font-size: 1.5rem; /* Adjust size as needed */
  color: #000000; /* Or your desired color */
  text-align: left; /* Or left, right as per design */
  margin-bottom: 4em; /* Space before the next element */
  max-width: 800px; /* Optional: constrain width */
  line-height: 1.3;
  font-weight: 500;
}

