/* Styles for the new abgd.it-style footer */
.abgd-style-footer {
  position: fixed; /* Keep the footer at the bottom */
  bottom: 0; /* Align to the bottom of the viewport */
  left: 0; /* Align to the left of the viewport */
  z-index: 1000; /* Ensure it's above other content, but potentially below a fixed header if they overlap */
  height: 47px; /* Explicit height for consistency with header */
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  padding: 15px 20px; /* Consistent padding with header */
  box-sizing: border-box;
  font-size: 12px; /* Consistent font size with header */
  text-transform: uppercase;
  color: #000; /* Black text */
  border-top: 1px solid transparent; /* Subtle line, similar to header's bottom border */
}

.footer-item {
  /* Basic styling for all items, can be overridden */
  padding: 0 5px; /* Consistent with header items */
}

.footer-social-links {
  display: flex;
  align-items: center;
  gap: 0.5em; /* Space between links and separators */
}

.footer-social-links a {
  text-decoration: none;
  color: inherit;
  transition: color 0.2s ease-in-out;
}

.footer-social-links a:hover {
  color: #000000; /* Slightly darker on hover, or choose another hover color */
}

.footer-social-links span {
  color: #000000; /* Color for the "|" separator */
}


/* Responsive adjustments for footer */
@media (max-width: 768px) {
  .abgd-style-footer {
    padding: 10px 15px; /* Slightly reduce padding on smaller screens */
    height: auto; /* Allow height to adjust to content if it wraps */
    min-height: 47px; /* Ensure it's at least the default height */
  }

  .footer-left-text,
  .footer-right-text {
    font-size: 10px; /* Slightly smaller font on mobile if needed */
    text-align: center; /* Center text if they stack */
  }
  
