/* Basic Reset / Defaults */
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f0f0f0; 
  color: #000; /* Black text */
}

.abgd-home-wrapper {
  display: flex;
  flex-direction: column;
  padding: 20px; /* General padding */
  box-sizing: border-box;
  background-color: #f0f0f0;
}

/* Header Styles */
.abgd-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-bottom: 20px;
  font-size: 0.75rem; 
  text-transform: uppercase;
  border-bottom: 1px solid #000000;
  margin-bottom: 40px;
}

.abgd-header .portfolio-title {
  font-weight: bold;
}

/* Main Content Styles */
.abgd-main-content {
  flex-grow: 1;
  width: 100%;
  /* max-width: 1200px; /* Removed to allow full width */
  /* margin: 0 auto; /* Removed for full width */
}

/* Unified Initials Section Styles */
.unified-initials-section {
  margin-bottom: 40px;
  display: flex;
  /* flex-direction is now handled by more specific rules or other stylesheets */
  align-items: flex-start; /* Left-align content within this section */
}

.unified-initials-section.home-page-initials {
  flex-direction: column; /* Apply column direction specifically for the home page */
}

/* Styles for the large text "CAL.D" */
.home-main-text {
  font-size: clamp(48px, 9vw, 160px); /* Responsive font size with min/max */
  font-weight: 500; /* Bold */
  color: #000; /* Black text */
  margin: 0; /* Reset default h1 margin */
  text-align: left; /* Ensure left alignment explicitly */
}

.equation-short {
  line-height: 0.7;
  margin-bottom: -19px;
  margin-left: -15px;
}

.equation-medium {
  line-height: 1.1;
  margin-bottom: -30px;
  margin-left: -10px;
  white-space: nowrap;
}

.equation-tall {
  line-height: 1.2;
  margin-bottom: -35px;
}

.equation-arrow {
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}

.text-details {
  display: flex;
  flex-direction: row; /* Arrange p tags in a line */
  justify-content: space-between; /* Distribute p tags evenly across the available width */
  align-items: baseline; /* Align text baselines */
  width: 100%; /* Ensure this container takes full width to space children across */
  padding-bottom: 7px;
  border-bottom: 1px solid #000000;
}

.text-details p {
  font-size: 0.875rem;
  text-transform: uppercase;
  margin: 0; 
  color: #000;
}

/* Description Container */
.description-container {
  display: flex;
  justify-content: space-between; /* Pushes items to the edges */
  align-items: flex-start;
  margin: 13vh 0 13vh auto; /* Aligns container to the right */
  max-width: 1300px;
  width: 90%;
}

.description-image {
  flex-basis: 45%; /* Give image more space */
  order: 2; /* Image on the right */
  max-width: 100%;
  height: auto;
  margin-right: 0;
}

/* Description Section */
.description-section {
  flex-basis: 40%; /* Make text section tighter */
  order: 1; /* Text on the left */
  margin-left: 10em;
  padding-right: 2em;
}

.description-section p {
  font-size: 1rem;
  line-height: 1.6;
  text-align: left; 
  
  
}

/* Bottom Section */
.bottom-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  border-top: 1px solid #000000; /* Subtle line */
  font-size: 0.875rem;
  text-transform: uppercase;
}

.portfolio-link {
  font-weight: bold;
  cursor: pointer; /* Indicate it's clickable */
}

/* Remove old styles if they exist and conflict */
.home-wrapper, .dot-pattern-background, .home-content, .layout-animation, .layout-element {
    /* If these were from the old Home.css, they might need to be removed or updated.
       For now, this new CSS is self-contained with new class names. */
}

@media (max-width: 768px) {
  .description-container {
    flex-direction: column; /* Stack items vertically */
    align-items: center; /* Center items horizontally */
    margin-left: 0; /* Reset margin-left for mobile */
    margin-top: 40px; /* Reduce margin-top for mobile */
    max-width: 100%; /* Allow full width */
  }

  .description-section {
    max-width: 90%; /* Allow text to take more width on mobile */
    /* order: 1; /* Ensure text is first if flex-direction was reversed, but not needed with column */
  }

  .description-image {
    width: 90%; /* Adjust image width for mobile */
    max-width: 400px; /* Max image width on mobile */
    margin-left: 0; /* Reset margin-left */
    margin-top: 20px; /* Add space above the image */
    /* order: 2; /* Ensure image is second */
  }

  .home-main-text {
    font-size: 15vw; /* Slightly larger for mobile if needed, or adjust as per design */
  }

  .text-details {
    flex-direction: column; /* Stack text details on mobile */
    align-items: flex-start; /* Align items to the start */
  }

  .text-details p {
    margin-bottom: 5px; /* Add some space between stacked p tags */
  }
}

/* Home Page Projects Overview Section */
.home-projects-overview-section {
  margin-top: 5px; /* Space above the projects section */
  margin-bottom: 40px; /* Space below the projects section */
  padding: 10px 0; /* Padding top and bottom */
}

.home-projects-overview-section h2 {
  font-size: 3.5rem; /* Large heading for the section */
  font-weight: 500;
  color: #000;
  text-align: left;
  margin-bottom: 30px; /* Space below the heading */
  text-transform: uppercase;
  padding-left: 20%; /* Indent with a percentage */
}

.home-projects-grid {
  display: grid;
  grid-template-columns: 1fr; /* Each project on its own line */
  gap: 30px; /* Space between cards */
}

.home-project-card-link {
  text-decoration: none; /* Remove underline from links */
  color: inherit; /* Inherit text color */
  display: block; /* Make the link a block to contain the card */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.home-project-card-link:hover,
.home-project-card-link:focus {
  /* No box-shadow or other effects here, as per request */
}

.home-project-card {
  padding: 25px 25px 25px 20%; /* Use percentage for left padding */
  height: 100%; /* Make cards in a row equal height if needed by content */
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* Pushes CTA to bottom */
  border-top: 1px solid #000000; /* Line separator at the top */
}

.home-project-card-link:hover .home-project-card {
  border-top: 2px solid #000; /* Bolder and darker top border on hover */
}

.home-project-card h3 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #000;
  margin-top: 0;
  margin-bottom: 15px;
}

.home-project-card p {
  font-size: 1rem;
  line-height: 1.6;
  color: #000000; /* Darker gray for text */
  margin-bottom: 20px;
  flex-grow: 1; /* Allows paragraph to take available space */
  max-width: 600px;
}

.home-project-card-cta {
  font-weight: bold;
  color: #000; /* Black color for CTA */
  text-transform: uppercase;
  font-size: 0.9rem;
  align-self: flex-start; /* Align CTA to the left */
}

