@media (max-width: 768px) {
  .home-projects-overview-section h2 {
    font-size: 2rem; /* Adjust heading size for mobile */
    padding-left: 0; /* Reset padding for mobile */
    text-align: center; /* Center title on mobile */
  }

  .home-project-card {
    padding: 25px; /* Reset padding for mobile */
    align-items: center; /* Center content within the card */
  }

  .home-project-card h3,
  .home-project-card p {
    text-align: center; /* Center text within these elements */
  }

  .home-project-card-cta {
    align-self: center; /* Center the CTA button */
  }

  .home-projects-grid {
    grid-template-columns: 1fr; /* Single column on smaller screens */
  }
}
