.navigation-container {
  position: fixed;
  top: 0;
  left: -100%;
  width: 80vw;
  height: 100vh;
  display: block;
  background-color: #f0f0f0;
  z-index: 1000;
  transition: left 0.3s ease-in-out;
  padding-top: 60px;
}

.navigation-container.open {
  left: 0;
}

.navigation-column {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  border-right: none;
}

.hamburger-icon {
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 1001;
  cursor: pointer;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}


.hamburger-icon div {
  width: 25px;
  height: 3px;
  background-color: black;
  margin: 5px 0;
}

.navigation-column ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.navigation-column li {
  margin-bottom: 15px;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 15px;
  color: black;
}

.navigation-column li a {
  color: black;
  text-decoration: none;
}

.navigation-column li a.active {
  text-decoration: underline;
}

.navigation-column li.selected {
  text-decoration: underline;
}

.main-content {
  margin-left: 0;
  width: 100vw;
  padding: 8px;
  box-sizing: border-box;
  transition: margin-left 0.3s ease-in-out;
}

@media (min-width: 768px) {
  .hamburger-icon {
    display: none;
  }

  .navigation-container {
    left: 0;
    width: 18vw;
    display: flex;
    padding-top: 0;
  }

  .navigation-column {
    width: 50%;
    border-right: transparent;
  }

  .main-content {
    margin-left: 18vw;
  }
}
